@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  padding: 0;
  font-family: 'Orbitron', sans-serif; }

.background {
  height: 100vh;
  background-color: black;
  background-image: url("http://getwallpapers.com/wallpaper/full/2/5/2/391171.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

nav.navbar {
  background-image: url("https://www.transparenttextures.com/patterns/brushed-alum.png"); }
  nav.navbar h1 {
    background: linear-gradient(to bottom, #0fbed3 25%, #0A7E8C 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 2px #000000; }

#keyboard {
  background-color: #b1bdc0;
  background-image: url("http://getwallpapers.com/wallpaper/full/7/d/2/159283.jpg");
  border-radius: 15px;
  border-bottom: #5a5a5a solid 1px;
  height: 275px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0 3px 0px 0px #afafaf, 0 6px 0px 0px #a0a0a0, 0 9px 0px 0px #8c8c8c, 0 12px 0px 0px #828282, 0 14px 0px 0px #787878; }
  #keyboard::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    height: 290px;
    border-radius: 15px;
    border-bottom: #000000 1px solid; }
  #keyboard #display {
    color: #053e45;
    letter-spacing: 2px;
    background-color: #b1bdc0;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    border-bottom: #ffffff solid 1px;
    -webkit-filter: blur(1px);
            filter: blur(1px); }

.drum-pad {
  cursor: pointer;
  border: #0A7E8C solid 1px;
  text-shadow: 0 -2px 0 black, 0 1px 1px #3edef1;
  font-size: 2em;
  font-weight: bold;
  color: #07535c;
  height: 50px;
  line-height: 50px;
  background: linear-gradient(to bottom, #0fbed3 25%, #0A7E8C 100%);
  border-radius: 5px;
  border-top: 1px solid #0c98a9;
  border-bottom: 1px solid #0da9bc;
  box-shadow: 0 4px 0 #07535c, 0 5px 10px rgba(0, 0, 0, 0.25);
  transition: all .2s; }
  .drum-pad:hover {
    background: linear-gradient(to bottom, #26daf0 30%, #0da9bc 80%); }
  .drum-pad:active {
    transform: translate(0px, 3px);
    border-bottom: 1px solid;
    box-shadow: none; }

